import { modalOpen, modalCloseFunc } from '@layouts/modal/modal';
export default () => {
  $('.form__submit').on('click', function (e) {
    let inputs = $(this).parent('form').find('.input'),
      inputArray = [];

    inputs.each(function (i) {
      if (!this.validity.valid) {
        $(this).addClass('invalid');
        inputArray[i] = false;
      } else {
        inputArray[i] = true;
      }
    });

    let formDone = Object.values(inputArray).every((val) => val !== false);

    if (formDone) {
      e.preventDefault();
      modalOpen(this);
    }
  });
  $('.input').on('invalid', function (e) {
    e.preventDefault();
  })
  $('.modal__button-close').on('click', function () {
    modalCloseFunc();
  });
};
