import { modalOpen, modalCloseFunc } from '@layouts/modal/modal';
export default () => {
  $('.form .input').on('input', function () {
    $(this).removeClass('invalid');
  })

  $('.button[data-modal="form"]').on('click', function () {
    modalOpen(this);
  });

  $('.modal__button-close').on('click', function () {
    modalCloseFunc();
  });
};
